	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Easter Monday","date":"17 Apr 2006","slug":"/2006-04-17-easter-monday/","tags":"Y2006"},"html":"<p>Easter Monday is one of those holidays we've never gotten into. We usually end up doing some sort of home improvement project like painting or wallpapering. Today we sorted out outdated toys, a big chore since Christopher seems to have inherited my sense of organizations (stack and pile). Our Tagesmutter will be happy for the pile of outsorted toys.</p>\n<p>It'd be great to be able to sit in the garden, but the weather is a month behind schedule and we've got standing water on the lawn. No walking on the grass without rubber boots. </p>\n<p>Christopher was home sick all last week, by Friday we were both tired of being stuck home together. He was healthy enough for the Easter fire Saturday night, but we didn't stay long. Yesterday we searched for Easter eggs in the mud and took Oma out for Easter dinner. </p>"},"next":{"fields":{"title":"Do Mention The War","date":"14 Apr 2006","slug":"/2006-04-14-do-mention-the-war/"}},"previous":{"fields":{"title":"First Slide: I Think We Need To Talk","date":"18 Apr 2006","slug":"/2006-04-18-first-slide-i-think-we-need-to-talk/"}}},{"node":{"fields":{"title":"Do Mention The War","date":"14 Apr 2006","slug":"/2006-04-14-do-mention-the-war/","tags":"Y2006"},"html":"<p><a href=\"http://msnbc.msn.com/id/12318337/\" title=\"World cup seen as chance to recognise Nazi past - Financial Times - MSNBC.com\">World cup seen as chance to recognise Nazi past</a>, which I guess means it's now OK for John Cleese to <a href=\"http://www.fawltysite.net/episode06.htm\">mention the war</a>, and for English fans to sing <a href=\"http://football.guardian.co.uk/Columnists/Column/0,,1731689,00.html\">\"Ten German Bombers\"</a>. (via <a href=\"http://www.anglofritz.com/2006/04/no_were_listening.html\">anglofritz</a>)</p>"},"next":{"fields":{"title":"Open Mabber","date":"12 Apr 2006","slug":"/2006-04-12-open-mabber/"}},"previous":{"fields":{"title":"Easter Monday","date":"17 Apr 2006","slug":"/2006-04-17-easter-monday/"}}},{"node":{"fields":{"title":"Open Mabber","date":"12 Apr 2006","slug":"/2006-04-12-open-mabber/","tags":"Y2006"},"html":"<p>Since the <a href=\"http://www.papascott.de/archives/2006/02/03/mabber-me/#comments\">Number 1 question</a> I get lately is whether I have any Mabber invitations, it's good news that as of now <a href=\"http://blog.mabber.com/eintrag.php?id=31\" title=\"Open Registration [mabber Blog]\">registration for Mabber is open</a>. Come <a href=\"http://invite.mabber.com/\">crash</a> the instant messaging party, no invitation is needed.</p>\n<p>In the spirit of <a href=\"http://www.joelonsoftware.com/articles/fog0000000012.html\" title=\"What is the Work of Dogs in this Country? - Joel on Software\">dogfood</a> I've been using Mabber daily since we starting testing. Not only does it work well, but if you add a little water it makes a very tasty <a href=\"http://www.gravytraindog.com/\">gravy</a>.</p>"},"next":{"fields":{"title":"I'm trying to think...","date":"11 Apr 2006","slug":"/2006-04-11-im-trying-to-think/"}},"previous":{"fields":{"title":"Do Mention The War","date":"14 Apr 2006","slug":"/2006-04-14-do-mention-the-war/"}}}],"pathPrefix":"","first":false,"last":false,"index":229,"pageCount":1101}}

